<template>
  <div class="ManualAudit phone-pad">
    <div class="title font-bold tl">{{wayIndex==1 ? '上传购车发票':'上传行驶证'}}</div>
    <!--上传购车发票-->
    <div class="payCarTicket-out box">
      <div @click.stop='openUploadDialog(1,(wayIndex==1 ? 2:3),0,false,carImage1)' :key="('img1_'+uploadImg1.url)" class="payCarTicket bgCss box" :style="{'background-image':'url('+(uploadImg1.res ? uploadImg1.localImg : wayIndex==1 ? ticketImg : carDrive)+')'}">
        <!-- <img v-if="!ticketShow" class="photo" src="../../assets/img/addCar/photo.png"/> -->
        <div class="repeat-upload tc">重新上传</div>
      </div>
    </div>
    <div class="title1 font-bold tl">请确认以下信息</div>
    <div class="list-pad">
      <div class="list flex space_between">
        <div class="list1 font-bold">姓名</div>
        <div v-if='isDisabled' @click.stop="disabledToast" class="list2 tr">请输入姓名</div>
        <input v-show="!isDisabled" @input="nameLen" placeholder="请输入姓名" v-model="uploadImg1.userName" type='text' class="list2 tr"/>
      </div>
      <div v-if="wayIndex==1" class="list flex space_between">
        <div class="list1 font-bold">证件号</div>
        <div v-if='isDisabled' @click.stop="disabledToast" class="list2 tr">请输入证件号</div>
        <input v-show="!isDisabled" @input="authNumberLen" placeholder="请输入证件号" v-model="uploadImg1.invoiceIdentificationNumber" type='text' class="list2 tr" />
      </div>
      <div class="list flex space_between">
        <div class="list1 flex">
          <div class='font-bold'>VIN码</div>
          <img @click.stop='errorStatus=3;noticeShow=true;' class="VIN-img" src="../../assets/img/addCar/help.png"/>
        </div>
        <div v-if='isDisabled' @click.stop="disabledToast" class="list2 VIN tr">请输入VIN</div>
        <input v-show='!isDisabled' @input="vinLen" placeholder="请输入VIN" v-model="uploadImg1.vin" type="text" class="list2 VIN tr"/>
      </div>
      <div v-if="wayIndex==1" class="list flex space_between">
        <div class="list1 font-bold">开票日期</div>
        <div class="list2 flex" @click.stop="mulAddData">
          <div class="text2" :class="(uploadImg1.invoiceDate ? 'text2Col':'')">{{uploadImg1.invoiceDate ? uploadImg1.invoiceDate :'请输入开票日期'}}</div>
          <img src="../../assets/img/addCar/click.png"/>
        </div>
      </div>
      <div v-if="wayIndex==2" class="list flex space_between">
        <div class="list1 font-bold">发证日期</div>
        <div class="list2 flex" @click.stop="mulAddData">
          <div class="text2" :class="(uploadImg1.licenseDate ? 'text2Col':'')">{{uploadImg1.licenseDate ? uploadImg1.licenseDate :'请输入发证日期'}}</div>
          <img src="../../assets/img/addCar/click.png"/>
        </div>
      </div>
      <div class="list flex space_between">
        <div class="text1 font-bold">选择城市</div>
        <div class="list2 flex" @click.stop='openCity'>
          <div class="text2" :class="(cityCol ? 'text2Col':'')">{{cityCol ? cityCol:'请选择城市'}}</div>
          <img src="../../assets/img/addCar/click.png"/>
        </div>
      </div>
      <div class="list flex space_between">
        <div class="list1 font-bold">入会经销商</div>
        <div class="list2 flex" @click.stop='gotoAgent'>
          <div class="text2" :class="(agentCol ? 'text2Col':'')">{{agentCol ? agentCol:'请选择经销商'}}</div>
          <img src="../../assets/img/addCar/click.png"/>
        </div>
      </div>
    </div>
    <div @click.stop='submitBtn' class="btn tc font-bold" :class="(btnCol ? 'btnCol':'')">
      <div class="phone-pad">
        <div class="mybtn">提交人工审核</div>
      </div>
    </div>
    <div v-if='noticeShow' class="credentials-bg hiddenbg tc">
      <div class="credentials-bg-main hiddenbg_div">
        <div class="credentials-bg-top">
          <div class="text1" v-if="errorCode=='1107'">检验失败</div>
          <div class="errorStatus-title font-bold" v-if="errorStatus==15">抱歉，暂时无法添加爱车</div>
		  <div v-if="errorStatus==15" v-html="errorMes" class="text2"></div>
          <div v-if="errorStatus!=3 && errorStatus!=13 && errorStatus!=4 && errorStatus!=15" :class="(errorCode=='1107' ? 'text2':'text3')">{{errorMes}}</div>
          <!-- <div v-if="errorStatus==13" class="text3">发布成功</div> -->
          <div v-if="errorStatus==13">
            <div class="text1">提交成功</div>
            <div class="text2">我们将尽快审核您的资料</div>
          </div>
          <div v-if="errorStatus==3" class="text3">
            车辆识别号码（ VIN码 ）<br/>是每辆车的唯一标识序列号<br/>通常在以下地方可以找到：
            <br/>
            <br/>
             1、车辆挡风玻璃 <br/>
             2、购车发票 <br/>
             3、行驶证<br/>
          </div>
          <div v-if='errorStatus==4' class="text1">校验失败</div>
          <!-- <div v-if='errorStatus==4' class="text2">请确认您的车辆信息是否正确，且购<br/>车人须与该账号实名认证信息一致</div> -->
          <div v-if="errorStatus==4" class="text2 tl">
            1.请保证图片焦点清晰，信息可读,不可太小或模糊不清<br/>
            2.不可有反光影响文字显示<br/> 
            3.请让四个边角完整展示，不要用手遮挡证件<br/> 
            4.请不要自行加水印<br/> 
            5.请不要使用扫描件或复印件<br/>
          </div>
        </div>
        <div class="flex font-bold toast-mybtn">
          <div @click.stop="closeNotice" class="munual-btn2">{{errorStatus==4 || (errorStatus!=3 && errorStatus!=13 && errorStatus!=14 && errorStatus!=15) ? '重新识别':'我知道了'}}</div>
        </div>
      </div>
    </div>
    <!--选择日期-->
    <DatetimePicker ref="date" @getDate="getDate"></DatetimePicker>
    <!--选择本地图片-->
    <uploadImage ref="uploadImage" @localImage='localImage' :uploadIndex="uploadIndex"></uploadImage>
    <!--选择城市-->
    <cityPicker @getCity="getCity" ref='city'></cityPicker>
    <!--选择经销商-->
    <!-- <agent v-if='city.province && agentShow' :city='city' @getAgentMes="getAgentMes"></agent> -->
  </div>
</template>

<script>
  import ticketImg from  '../../assets/img/addCar/payCarTicket.png';
  import carDrive from  '../../assets/img/addCar/carDrive.png';
  import datePicker from 'mixins/datePicker.js'
  import uploadImgMixins from 'mixins/uploadImgMixins.js'
  import cityPicker from '@/cityPicker/index.vue'
  // import agent from '@/agent/index.vue'
  
  import {bindVehicleApply,getDealerCityList} from 'fetch/api.js'
  import bridge from 'fetch/bridge.js'
  let Base64 = require('js-base64').Base64
  
  export default{
    name:'ManualAudit',
    mixins:[datePicker,uploadImgMixins],
    components:{
      cityPicker,
      // agent,
    },
    data(){
      return {
        ticketImg,
        carDrive,
        wayIndex:1,
        uploadImg1:{url:'',res:false,localImg:'',file:'',index:0},
        city:{},//城市
        agentShow:false,//是否展示经销商
        agentChoose:{},//入会经销商
        noticeShow:false,
        errorMes:'',
        errorStatus:-1,
        type:'',
        preventRes:false,
        errorCode:'',
        indexIs:1,
      }
    },
    activated(){
      // this.noticeShow=true
      // this.errorStatus=15;//测试
      // //this.errorCode='1107'
      // this.errorMes='这是个错'
      //this.uploadImg1={url:'',res:false,localImg:'',file:'',index:0};
      
      setTimeout(()=>{
        let myAgent=sessionStorage.myAgent;
        if(myAgent){
          myAgent=JSON.parse(myAgent)
          this.agentChoose=myAgent;
          sessionStorage.myAgent='';
          return;
        }
        
        this.type=this.$route.query.type;
        console.log('当前页面的url==',window.location.href,this.$route.query,'sessionStorage.uploadImg=',sessionStorage.uploadImg,
        'sessionStorage.isMu=',sessionStorage.isMu)
        this.preventRes=false
        this.onGetImgKey=false;
        this.wayIndex=parseFloat(this.$route.query.wayIndex)
        this.carIndex=parseFloat(this.$route.query.carIndex)
        if(sessionStorage.isMu!=1){
          sessionStorage.isMu=0;
          if(sessionStorage.uploadImg){
            this.uploadImg1=JSON.parse(sessionStorage.uploadImg)
            this.uploadImg1.file=this.$store.state.imgFile;
            this.indexIs=this.uploadImg1.index;
            console.log('获取到的图片','uploadImg1=',this.uploadImg1,'file=',this.uploadImg1.file,'store=',this.$store.state)
            this.setlocalImage(this.uploadImg1.file)
            let {vin,url,userName,invoiceDate,invoiceIdentificationNumber,licenseDate}=this.uploadImg1;
            if(this.indexIs==1){//发票
              if(!vin && !userName && !invoiceDate && !invoiceIdentificationNumber){
                this.ticketDisabled=true;
              }else{
                this.ticketDisabled=false;
              }
            }else if(this.indexIs==2){
              if(!vin && !userName && !licenseDate){
                this.cardDisabled=true;
              }else{
                this.cardDisabled=false;
              }
            }
          }else{
            if(this.indexIs==1){//发票
              this.ticketDisabled=true
            }else if(this.indexIs==2){
              this.cardDisabled=true;
            }
          }
        }
        
        let city=sessionStorage.getItem('city')
        let agent=sessionStorage.getItem('agent')
        if(city){
          this.city=JSON.parse(city)
        }
        if(agent){
          this.agentChoose=JSON.parse(agent)
        }
        //this.noticeShow=true;this.errorStatus=14;this.errorMes=111;
      },100)
      
      //this.getDealerCityList()//获取经销商
      // this.noticeShow=true;
      // this.errorMes="获取经销商";//测试
    },
    mounted(){
      this.$refs.city.getDealerCityList()
    },
    computed:{
      agentCol(){//是否选择了入会经销商
        return this.agentChoose.shortName
      },
      cityCol(){
        return this.city.province+this.city.city
      },
      isDisabled(){
        let index=this.indexIs;//是行驶证还是发票
        console.log('是否禁止输入=',{index,ticketDisabled:this.ticketDisabled,cardDisabled:this.cardDisabled})
        if(index==1 && this.ticketDisabled){
          return true
        }else if(index==2 && this.cardDisabled){
          return true
        }
        return false;
      },
      btnCol(){
        let mes=this.uploadImg1
        let index=this.indexIs;//是行驶证还是发票
        
        //let res=this.agentCol && this.cityCol && mes.vin && mes.plateNumber && mes.url && mes.userName;
        let res=this.agentCol && this.cityCol && mes.vin && mes.url && mes.userName;
        console.log('index==',index,'mes=',mes,'res=',res)
        if(index==1){//发票
        console.log('this.agentCol=',this.agentCol,'this.cityCol=',this.cityCol,'mes.vin=',mes.vin,'mes.url=',mes.url,'mes.userName=',mes.userName,'mes.invoiceDate=',mes.invoiceDate,'mes.invoiceIdentificationNumber=',mes.invoiceIdentificationNumber)
          return res && mes.invoiceDate && mes.invoiceIdentificationNumber;
        }else if(index==2){
          //return res && mes.licenseDate && mes.licenseFullName
          return res && mes.licenseDate;
        }
        return false;
      },
    },
    methods:{
      disabledToast(){
        //请先上传购车发票/行驶证
        let text='请先上传'
        if(this.indexIs==1){
          text=text+'购车发票'
        }else{
          text=text+'行驶证'
        }
        this.$Toast(text)
      },
      mulAddData(){
        if(!this.isDisabled){
          this.dateDialogShow('0');
        }else{
          this.disabledToast();
        }
      },
      authAction(eventName){//埋点
        if(this.$route.query.test==1 || this.type=='app'){
          try{
            bridge('authAction',{eventName})
          }catch(err){
            console.log('埋点失败--',eventName)
          }
        }
      },
      vinLen(){//公司vin
        let vin=this.uploadImg1.vin.trim();
        if(vin.length>17){
          vin=vin.substring(0,17)
        }
        this.uploadImg1.vin=vin;
      },
      nameLen(){//姓名：15个字
        let name=this.uploadImg1.userName.trim();
        if(name.length>50){
          name=name.substring(0,50)
        }
        this.uploadImg1.userName=name;
      },
      authNumberLen(){//证件号
        let num=this.uploadImg1.invoiceIdentificationNumber.trim();
        if(num.length>18){
          num=num.substring(0,18)
        }
        this.uploadImg1.invoiceIdentificationNumber=num;
      },
      otherCallFn(index,val){
        console.log('时间选择==',index,val)
        if(index==0){
          let uploadImg1=this.uploadImg1
          uploadImg1.invoiceDate=val;
          uploadImg1.licenseDate=val;
          this.uploadImg1={...uploadImg1}
          console.log('this.uploadImg1=',this.uploadImg1)
        }
      },
      closeNotice(){
        if(this.errorStatus==13){
          this.authAction('mycar_mycar_add_laborcheck_sbumit_done')
          if(this.$route.query.type=='app'){//app跳转的
            bridge('finishPage')
          }else{
			  if(sessionStorage.isminiType=='wechatMini'){
			    //wx.miniProgram.redirectTo({ url: localStorage.getItem('myhref')});
			    wx.miniProgram.navigateBack()
				return;
			  }
            //window.location.replace(localStorage.getItem('myhref'))
            if (typeof WeixinJSBridge==='object'){
              document.addEventListener('WeixinJSBridgeReady', function(){ WeixinJSBridge.call('closeWindow'); }, false);
              WeixinJSBridge.call('closeWindow');
            }else{
              window.opener=null;
              window.open('about:blank','_self','').close()
            }
          }
        }
        this.noticeShow=false;
        this.errorStatus=-1
        this.errorCode=''
      },
      openCity(){
        this.$refs.city.changeShow()
      },
      getCity(city){
        console.log('getCity==',city)
        sessionStorage.isMu=1;
        this.city=city;
        this.agentChoose={};
        //this.agentShow=true;
      },
      getAgentMes(data){//得到经销商信息
        if(data){
          this.agentChoose=data;
        }
        this.agentShow=false;
        console.log('得到经销商信息=data=',data)
      },
      async getDealerCityList(){//获取经销商
        let res=await getDealerCityList()
        if(res && res.resultCode=='0000'){
          
        }
      },
      setlocalImage(file){
        if(!file) return;
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          this.uploadImg1.localImg=reader.result;
        };
      },
      async submitBtn(){
        if(!this.btnCol) return;
        if(this.preventRes) return;
        this.preventRes=true;
        this.authAction('mycar_mycar_add_laborcheck_sbumit')
        let mes={...this.uploadImg1};
        let vinReg=/((?=.*[a-zA-Z])(?=.*[0-9]))^[a-zA-Z0-9]{17}$/;
        if(!vinReg.test(mes.vin)) {
          this.preventRes=false
          return this.$Toast('VIN码填写不正确');
        }
        
        let wayIndex=this.wayIndex;
        let params={
          dealerCode:this.agentChoose.dealerCode,
          vin:Base64.encode(mes.vin),
          // plateNumber:Base64.encode(mes.plateNumber),
          vehicleType:parseFloat(this.carIndex)+1,//车辆类型
        }
        if(mes.plateNumber){
          params.plateNumber=Base64.encode(mes.plateNumber);
        }
        console.log('params==',params)
        //params.vin=mes.vin;
        if(wayIndex==1){//购车发票
          params.invoiceImgUrl=mes.url;
          params.invoiceDate=mes.invoiceDate+' 00:00:00';
          params.invoiceFullName=Base64.encode(mes.userName);
          params.invoiceIdentificationNumber=Base64.encode(mes.invoiceIdentificationNumber)
          console.log(Base64.decode(params.invoiceFullName))
          console.log(Base64.decode(params.vin))
          console.log(Base64.decode(params.invoiceIdentificationNumber))
        }else{//行驶证
          params.vehicleLicenseUrl=mes.url;
          params.licenseDate=mes.licenseDate+' 00:00:00';
          params.licenseFullName=Base64.encode(mes.userName);
          // params.orgName=mes.orgName;//企业名称
          // params.orgEmail=Base64.encode(mes.orgEmail);
          // params.orgCode=Base64.encode(mes.orgCode);
          // params.authorizationImgUrl=mes.authorizationImgUrl;
          // params.businessLicenseImgUrl=mes.businessLicenseImgUrl;
          console.log(Base64.decode(params.licenseFullName))
          console.log(Base64.decode(params.vin))
        }
        console.log('params',params,mes)
        // console.log(Base64.decode(params.licenseFullName))
        // console.log(Base64.decode(params.vin))
        //return console.log('params',params)
        this.$LoadingShow('加载中')
        if(this.type=='app'){
          bridge(
          "receiveLoadNetworkArgument",
          {
            path:'userInfo/bindVehicleApply',gateway:"private",params:params,method:"POST"
          },
          {
            callback:this.bindVehicleApplyCall,winName:'acceptNewEnergyResultSet'
          }
          );
        }else{
          let res=await bindVehicleApply(params)
          this.bindVehicleApplyCall(res)
        }
      },
      bindVehicleApplyCall(res){
        console.log('提交res=',res)
        this.noticeShow=true;
        this.preventRes=false
        this.$LoadingHide()
        if(res && res.resultCode=='0000'){
          console.log('提交成功==');
          //this.$Toast('提交成功');
          this.errorStatus=13;
          // setTimeout(()=>{
          //   window.location.replace(localStorage.getItem('myhref'))
          // },2000)
        }else{
          let code=res.resultCode;
          if(code=='1107'){
            this.errorCode=code
          }else{
			  if(code=='1090'){
				  if(this.type!='app'){
					 res.message=res.message.replace(/(\n|\r|\\n|\\\\n)/g,'<br/>')
					 res.message=res.message.replace(/\d+-\d+-\d+/g,function($1,$2){
						console.log('进行处理==',{$1,$2})
						return `<a style="color:#005FBE;" href="tel:${$1}">${$1}</a>`
					 }) 
				  }
				  this.errorStatus=15;
			  }else{
				  this.errorStatus=14;
			  }
          }
          // this.$Toast(res.message);
          this.errorMes=res.message;
        }
      },
      dateTest(str){//校验日期2018-04-16
        let reg=/^\d{4}-\d{2}-\d{2}$/;
        if(reg.test(str)){
          return true;
        }
        return false;
      },
      getUploadMes(res,uploadIndex){
        console.log('获取到证件信息0000=',res,uploadIndex)
        let index=this.indexIs;
        if(res && res.resultCode=='0000'){
          let {data,key}=res.data;
          let mes={}
          if(uploadIndex==1 || uploadIndex==2){
            mes=JSON.parse(Base64.decode(data))
          }
          // this.uploadImg1.res=true;
          // this.uploadImg1.url=key
          // let index=this.uploadImg1.index
          let wayIndex=this.wayIndex;
          let carIndex=this.carIndex;
          
          console.log('解析的数据=',mes,'index=',index)
          let myfile=this.uploadImg1.file;
          //if(index==1){
          if(wayIndex==1){
            let {id_number,vehicle_invoice_buyer,vehicle_invoice_issue_date,vehicle_invoice_buyer_id,vehicle_invoice_car_vin,vehicle_invoice_engine_id}=mes
            if(!this.dateTest(vehicle_invoice_issue_date)){//校验日期
              vehicle_invoice_issue_date='';
            }
            if(!vehicle_invoice_buyer && !vehicle_invoice_issue_date && !vehicle_invoice_buyer_id && !vehicle_invoice_car_vin && !vehicle_invoice_engine_id){
              
              this.uploadImg1={url:'',res:false,localImg:'',file:''};
              this.errorStatus=4;
              this.noticeShow=true;
              this.ticketDisabled=true;
              return
            }
            this.ticketDisabled=false;
            
            this.uploadImg1={...this.uploadImg1,id_number,invoiceFullName:vehicle_invoice_buyer,userName:vehicle_invoice_buyer,
            invoiceDate:vehicle_invoice_issue_date,invoiceImgUrl:key,invoiceIdentificationNumber:vehicle_invoice_buyer_id,
            vin:vehicle_invoice_car_vin,plateNumber:vehicle_invoice_engine_id,res:true,url:key,
            }
            
            // this.uploadImg1.id_number=id_number;
            // this.uploadImg1.invoiceFullName=vehicle_invoice_buyer;
            // this.uploadImg1.userName=vehicle_invoice_buyer;
            // this.uploadImg1.invoiceDate=vehicle_invoice_issue_date;
            // this.uploadImg1.invoiceImgUrl=key;
            // this.uploadImg1.invoiceIdentificationNumber=vehicle_invoice_buyer_id;//购车发票证件号码
            // this.uploadImg1.vin=vehicle_invoice_car_vin;
            // this.uploadImg1.plateNumber=vehicle_invoice_engine_id;
          }else if(wayIndex==2){
            //uploadImg2:{url:'',res:'',localImg:'',validity:'',startTime:'',endTime:''}
            let {validity,issue_date,vehicle_license_main_register_date,vehicle_license_main_vin,vehicle_license_main_plate_num,
            vehicle_license_main_owner}=mes
            if(!this.dateTest(issue_date)){//校验日期
              issue_date='';
            }
            if(!issue_date && !vehicle_license_main_vin && !vehicle_license_main_plate_num && 
            !vehicle_license_main_owner){
              this.uploadImg1={url:'',res:false,localImg:'',file:'',index};
              this.errorStatus=4;
              this.noticeShow=true;
              this.cardDisabled=true;
              return
            }
            this.cardDisabled=false;
            
            this.uploadImg1={...this.uploadImg1,validity,licenseDate:issue_date,vin:vehicle_license_main_vin,
            plateNumber:vehicle_license_main_plate_num,licenseFullName:vehicle_license_main_owner,userName:vehicle_license_main_owner,
            vehicleLicenseUrl:key,res:true,url:key,
            }
            
            // this.uploadImg1.validity=validity;
            // this.uploadImg1.licenseDate=issue_date;
            // this.uploadImg1.vin=vehicle_license_main_vin
            // this.uploadImg1.plateNumber=vehicle_license_main_plate_num;
            // this.uploadImg1.licenseFullName=vehicle_license_main_owner;
            // this.uploadImg1.userName=this.uploadImg1.licenseFullName;
            // this.uploadImg1.vehicleLicenseUrl=key;//照片
          }
          console.log('改变数据=this.uploadImg1=',this.uploadImg1)
        }else{
          this.uploadImg1={url:'',res:false,localImg:'',file:'',index};
          this.noticeShow=true;
          this.errorMes=res.message;
        }
      },
      gotoAgent(){
        // this.agentShow=true;
        if(this.cityCol){
          //return this.agentShow=true;
          sessionStorage.city=JSON.stringify(this.city);
          return this.$router.push({path:'/agent',query:{}})
        }
        this.$Toast('请先选择城市')
      },
    },
  }
</script>

